.App {
  text-align: center;

}
#root{
  user-select: none;    
  background: linear-gradient(184deg, #fe9a9a, #fff);
}
#PlayBtn:hover{
  color: #61dafb;
}
.start .btn{
  margin: auto;
  margin-top: -20px;
  margin-bottom: 20px;
  display: block;
  font-size: x-large;
  padding: 5px 15px;
  
  background-color: black;
}
html{
  /* background: linear-gradient(0deg, #dc6411, #dc6411); */
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
;

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.cards{
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  flex-wrap: wrap;
  gap: 5px;
  padding: 15px;
  align-items: center;
  justify-content: center;
  transition-duration: 0.4s;
  
}
.btn-outline-info{
}
.card:nth-child(2n-1){
  background-color: #ed8449db;
}
.card{
  background-color: #cb77e0;
}
.cards strong{
  z-index: -1;
  transform: scale(0);
  transition-duration: 0.4s;
}

.flipped{
  z-index: 1 !important;
  transform: scale(1.2) !important;
}


.App-link {
  color: #61dafb;
}

.Score h1{
  font-family: 'Cinzel', serif;
  text-align: center;
  font-weight: 700;
}
.Matrix{
  margin-top: 45px;
}
.Game > h1{
  padding:0px 15px;
  font-family: 'Cherry Bomb One', cursive;
  font-size:5vw;
  color:black;
  text-align: center;
}

.Game > h2{
  text-align: center;
  font-family: 'Cinzel', serif;
  padding:0px 15px;
  /* font-weight:; */
  font-size:medium;
  font-weight: 600;

}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media (width < 550px){

  .cards{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding: 10px;
  }
  .card{
    width: 100% !important;
    padding: 5px;
  }
  .card strong{
    
    font-size: 8vw !important;
    
  }
  .Game > h1{
    font-family: 'Cherry Bomb One', cursive;
    font-size:9vw;
    
    color:black;
  }
  .Game > h2{
    font-size:4vw;
    font-weight: normal !important;
    color:black;
  }
}